//员工管理
import http from '../httpConfig'

const table = "mrbase_sys_swift_config"


// 获取所有流水号配置
export const getserialNumberList = (params = {}) => {
  return new Promise((resolve, reject) => {
    http.post('/api/common/serialNumberAll/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

