<template>
  <div>
    <!--   @click:row='godetail' -->
    <mrc_ess_table v-if="$route.name=='serial-number'"
                   :headers='headers'
                   :hideAddBtn='!hasBtn("add-serial-number")'
                   pageName='addSerialNumber'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['org_level_uuid']"
                   :items='desserts'>

      <template v-slot:top>
        <v-card-title class="cardtitle"
                      style="height:60px">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
        </v-card-title>
      </template>

      <template v-slot:item.org_level_uuid='{item}'>
        <v-chip v-for="(item1,index) in item.org_level_uuid"
                :key="item1+index">
          {{item1}}
        </v-chip>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['viewserial-number',
                                'edit-serial-number',
                                'delete-serial-number']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getserialNumberList
  , createSerialNumber
  , detailSerialNumber
  , addSerialNumber
  , updateSerialNumber
  , deleteSerialNumber
} from '@/service/api/serialNumber.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      stepIndex: 1,
      dialog: false,
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    menulist () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('viewserial-number') },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-serial-number') },
        { name: this.$t('label_delete'), show: this.hasBtn('delete-serial-number') },
      ]
    },
    headers () {
      return [
        { text: this.$t('label_SwiftName'), align: 'start', value: 'swift_config_name', sortable: false },
        { text: this.$t('label_Preview'), value: 'swift_config_preview', sortable: false, align: 'center', },
        { text: this.$t('label_company'), value: 'org_level_uuid', sortable: false, align: 'center', },
        { text: '', value: 'actions', sortable: false, align: 'end', width: '100' },
      ]
    },
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'serial-number') {
        this.getlist()
      }
    }
  },
  mounted () {
    this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getserialNumberList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200 && res.data.length > 0) {
        this.desserts = res.data.map(item => {
          item.org_level_uuid.indexOf(',') == -1 ? item.org_level_uuid = item.org_level_uuid.split(',') : item.org_level_uuid = [item.org_level_uuid]
          return item
        })
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    addSerialNumber () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addSerialNumber'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', item.swift_config_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailSerialNumber'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.$store.commit('set_subPage_name', item.swift_config_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailSerialNumber'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteSerialNumber({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    },
    success () { },
    cancel () { }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
.dowanTemplate {
  position: absolute;
  right: 180px;
  top: -50px;
}
</style>
